.modal-form {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 320px;
  background-color: @white;
  text-align: center;
  box-shadow: 0 7px 15px fade(@white, 10%);
  /*у меня тут тень не получается*/
  border-radius: 1px;

  @media (min-width: @tablet-width) {
    width: 460px;
  }

  @media (min-width: @desktop-width) {
    width: 780px;
  }
}

.modal-form--success {
  display: none;
}

.modal-form--failure {
  display: none;
}

.modal-form__top {
  padding: 23px 20px 24px 20px;
}

.modal-form__bottom {
  padding: 48px 20px 52px 20px;
  display: flex;
  justify-content: center;
}

.modal-form__bottom--success {
  background-color: @bg-color-main;
}

.modal-form__title {
  font-size: 18px;
  line-height: 24px;
  color: @black;
}

.modal-form__text {
  line-height: 24px;
  color: @main-font-and-bg;
}
